import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { dev.bitspittle.site.pages.IndexPage() }
        ctx.router.register("/blog/") { dev.bitspittle.site.pages.blog.BlogListingsPage() }
        ctx.router.register("/blog/2023/hub_site") {
                dev.bitspittle.site.pages.blog._2023.Hub_sitePage() }
        ctx.router.register("/product/") { dev.bitspittle.site.pages.product.IndexPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.initSilkHook = { ctx ->
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.layouts.CenterColumnStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.sections.FooterStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.sections.CopyrightStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.sections.NavHeaderStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.sections.NavLinkStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.sections.NavButtonStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleListStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleSectionStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleTitleStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleMetaStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleAuthorStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleDateStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleNameStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleUpdatedStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.blog.ArticleDescStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.code.CodeBlockStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.code.InlineCodeStyle)
        ctx.theme.registerComponentStyle(dev.bitspittle.site.components.widgets.navigation.HoverLinkStyle)
        ctx.theme.registerComponentVariants(dev.bitspittle.site.components.sections.LogoVariant)
        dev.bitspittle.site.initSilk(ctx)
        dev.bitspittle.site.components.layouts.initHighlightJs(ctx)
        dev.bitspittle.site.components.sections.initNavHeaderStyles(ctx)
        dev.bitspittle.site.components.widgets.navigation.initHeaderLinkInteraction(ctx)
    }

    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will overwrite them
    // the first time we render this page with their composable, dynamic versions. Think of this as
    // poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) {
        root.removeChild(root.firstChild!!)
    }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(
            AppGlobalsLocal provides mapOf("title" to "HaMiDo")
        ) { dev.bitspittle.site.MyApp {
              router.renderActivePage { renderWithDeferred { it() } }
        } }
    }
}
